<template>
  <div>
    <div class="category">
      <div class="category_table">
        <v-data-table
          :headers="headers"
          :items="dept1"
          :items-per-page="10"
          hide-default-footer
        >
          <template v-slot:top>
            <v-toolbar flat color="blue darken-3">
              <v-toolbar-title class="white--text font-weight-medium"
                >1 Dept</v-toolbar-title
              >
              <v-spacer></v-spacer>
              <v-dialog v-model="dialog" max-width="400px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn color="white" v-bind="attrs" v-on="on">
                    <v-icon left dark> mdi-plus </v-icon>
                    Add
                  </v-btn>
                </template>
                <v-card>
                  <v-card-title>
                    <span class="text-h5">{{ formTitle }}</span>
                  </v-card-title>

                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col cols="12" sm="12" md="12">
                          <v-text-field
                            v-model="editedItem.name"
                            label="Name"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">
                      Cancel
                    </v-btn>
                    <v-btn color="blue darken-1" text @click="save">
                      Save
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
              <v-dialog v-model="dialogDelete" max-width="400px">
                <v-card>
                  <div class="card_title text-center mb-8">
                    Are you sure you want to delete this item?
                  </div>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" @click="closeDelete">Cancel</v-btn>
                    <v-btn color="primary" @click="deleteItemConfirm"
                      >Delete</v-btn
                    >
                    <v-spacer></v-spacer>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>
          <template v-slot:item.name="{ item }">
            <div class="category_value">
              {{ item.name }}
              <div class="category_move">⋮</div>
            </div>
          </template>
          <template v-slot:item.actions="{ item }">
            <v-icon small class="mr-2" @click="editItem(item)">
              mdi-pencil
            </v-icon>
            <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
          </template>
          <template v-slot:no-data> </template>
        </v-data-table>
        <v-btn outlined class="mb-3 mt-3"> Completed </v-btn>
      </div>
      <div class="category_table">
        <v-data-table
          :headers="headers"
          :items="dept2"
          :items-per-page="10"
          hide-default-footer
        >
          <template v-slot:top>
            <v-toolbar flat color="blue darken-3">
              <v-toolbar-title class="white--text font-weight-medium"
                >Dept 2</v-toolbar-title
              >
              <v-spacer></v-spacer>
              <v-btn color="white">
                <v-icon left dark> mdi-plus </v-icon>
                Add
              </v-btn>
            </v-toolbar>
          </template>
          <template v-slot:item.name="{ item }">
            <div class="category_value">
              {{ item.name }}
              <div class="category_move">⋮</div>
            </div>
          </template>
          <template v-slot:item.actions="{ item }">
            <v-icon small class="mr-2"> mdi-pencil </v-icon>
            <v-icon small> mdi-delete </v-icon>
          </template>
          <template v-slot:no-data> </template>
        </v-data-table>
      </div>
      <div class="category_table">
        <v-data-table
          :headers="headers"
          :items="dept2"
          :items-per-page="10"
          hide-default-footer
        >
          <template v-slot:top>
            <v-toolbar flat color="blue darken-3">
              <v-toolbar-title class="white--text font-weight-medium"
                >Dept 3</v-toolbar-title
              >
              <v-spacer></v-spacer>
              <v-btn color="white">
                <v-icon left dark> mdi-plus </v-icon>
                Add
              </v-btn>
            </v-toolbar>
          </template>
          <template v-slot:item.name="{ item }">
            <div class="category_value">
              {{ item.name }}
              <div class="category_move">⋮</div>
            </div>
          </template>
          <template v-slot:item.actions="{ item }">
            <v-icon small class="mr-2"> mdi-pencil </v-icon>
            <v-icon small> mdi-delete </v-icon>
          </template>
          <template v-slot:no-data> </template>
        </v-data-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    dialogDelete: false,
    headers: [
      {
        text: "Value",
        align: "start",
        value: "name",
      },
      { text: "", value: "actions", align: "end", sortable: false },
    ],
    dept1: [
      {
        name: "Windshield Protection",
      },
      {
        name: "Paint Protection Film",
      },
      {
        name: "Window Film",
      },
    ],
    dept2: [],
    editedIndex: -1,
    editedItem: {
      name: "",
    },
    defaultItem: {
      name: "",
    },
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "New" : "Edit";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  methods: {
    editItem(item) {
      this.editedIndex = this.dept.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.dept.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.dept.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.dept[this.editedIndex], this.editedItem);
      } else {
        this.dept.push(this.editedItem);
      }
      this.close();
    },
  },
};
</script>

